@charset "UTF-8";
:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --text-black: #22252F;
  --border-gray: #2D333C;
  --gray-400: #E2E3EA;
  --text-gray-light: #7F808A;
  --text-gray: #8E8F9E;
  --bg-soft: #efefef;
  --bg-blue: #e0f2f7;
  --bg-green: #e6ffe6;
  --bg-red: #ffe6e6;
  --bg-hover: #f9f9f9;
  --color-green: #008000;
  --color-red: #eb3423;
}

.video-wrapper {
  border-radius: 20px;
  height: 321px;
  background: #000;
  /* чтоб поля были черными, если пропорции не совпадают */
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.video-wrapper .start-video {
  height: 100%;
  object-fit: contain;
  /* показать видео целиком без обрезки */
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-43%, -50%);
}

@media screen and (min-width: 451px) {
  .barrier-configurator .video-wrapper {
    border-radius: 20px;
    height: 450px;
  }
  .barrier-configurator .video-wrapper .start-video {
    transform: translate(-50%, -50%);
  }
}

@media screen and (min-width: 902px) {
  .barrier-configurator .video-wrapper {
    border-radius: 40px;
    height: 640px;
  }
  .barrier-configurator .video-wrapper .start-video {
    height: auto;
    transform: translate(-43%, -50%);
  }
}

@media screen and (min-width: 1367px) {
  .barrier-configurator .video-wrapper {
    height: 640px;
  }
  .barrier-configurator .video-wrapper .start-video {
    width: 100%;
    left: 0;
    transform: translate(0, -50%);
  }
}
